.homeContainer {
  display: flex;
  width: 100%;
}

.topbarContainer {
  height: 50px;
  width: 100%;
  background-color: #f08d7e;
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  border-radius: 5px;
  z-index: 999;
}

.topbarLeft {
  flex: 3;
  margin-right: 5px;
}

.logo {
  font-size: 24px;
  margin-left: 20px;
  font-weight: bold;
  color: white;
  cursor: pointer;
}

.topbarCenter {
  flex: 5;
}

.searchbar {
  width: 100%;
  height: 30px;
  background-color: #e2bab1;
  border-radius: 30px;
  display: flex;
  align-items: center;
}

.searchIcon {
  font-size: 20px !important;
  margin-left: 10px;
}

.searchInput {
  border: none;
  width: 70%;
  background-color: #e2bab1;
  color: #4e261d;
}
.searchInput:focus {
  outline: none;
}

.topbarRight {
  flex: 4;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: white;
  margin-left: 5px;
}

.topbarLink {
  margin-right: 10px;
  font-size: 14px;
  cursor: pointer;
}

.topbarIcons {
  display: flex;
}

.topbarIconItem {
  margin-right: 15px;
  cursor: pointer;
  position: relative;
}

.topbarIconBadge {
  width: 15px;
  height: 15px;
  background-color: red;
  border-radius: 50%;
  color: white;
  position: absolute;
  top: -5px;
  right: -5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}

.topbarImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
  /* position: absolute; */
  right: 10px;
}

.topbarImg:hover {
  width: 50px;
  height: 50px;
  /* position: absolute; */
  right: 1px;
  /* position: static; */
}

@media screen and (max-width: 600px) {
  .topbarContainer {
    width: 100%;
  }
  .searchInput:focus {
    width: auto;
  }
  .searchInput {
    display: none;
  }
  .topbarCenter {
    flex: 1;
  }
  .searchIcon {
    align-items: center;
    justify-content: center;
  }
  .topbarCenter .searchIcon {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: 5px;
  }

  .searchbar {
    width: 100px;
  }

  .logo {
    font-size: 18px;
    margin-left: 10px;
    font-weight: bold;
    color: white;
    cursor: pointer;
  }
  .topbarLeft {
    flex: 1;
    margin-right: 5px;
  }
  .topbarLink {
    margin-right: 3px;
    font-size: 14px;
    cursor: pointer;
  }
}
