.login{
    width: 100vw;
    height: 100vh;
    background-color: #f5e6dc;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loginWrapper{
    width: 70%;
    height: 70%;
    /* background-color: #E3826F; */
    display: flex;
}

.loginLeft, .loginRight {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.loginLogo{
    font-size: 50px;
    font-weight: 800;
    color: #E3826F;
    margin-bottom: 10px;
}

.loginDesc{
    font-size: 24px;
}

.loginBox{
    height: 400px;
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.loginInput{
    height: 50px;
    border-radius: 10px;
    border: 1px solid #E3826F;
    font-size: 18px;
    padding-left: 20px;
}

.loginInput:focus{
    outline: none;
}

.loginButton{
    height: 50px;
    border-radius: 10px;
    border: none;
    background-color: #e3826fe5;
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer; 
}

.loginForgot{
    text-align: center;
    color: #E3826F;
    font-weight: 400;
}

.loginRegisterButton{
    height: 50px;
    border-radius: 10px;
    border: none;
    background-color: #EFBA97 ;
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer; 
}
.loginRegisterButton:hover{
    background-color: #e3826fe5;
}
